import * as React from "react"
import { Link, graphql } from "gatsby"

import Bio from "../components/bio"
import Layout from "../components/layout"
import Seo from "../components/seo"
import * as homeStyles from "../components/home.module.css"

const BlogIndex = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata?.title || `Title`
  const posts = data.allMarkdownRemark.nodes

  console.log(posts)

  const bugListPosts = posts.filter(post =>
    post.fields.slug.includes("/bugLists")
  )
  const reviewPosts = posts.filter(post => post.fields.slug.includes("/review"))
  const jsPosts = posts.filter(post => post.fields.slug.includes("/javascript"))
  const dockerPosts = posts.filter(post => post.fields.slug.includes("/docker"))

  if (posts.length === 0) {
    return (
      <Layout location={location} title={siteTitle}>
        <Seo title="All posts" />
        <Bio />
        <p>
          No blog posts found. Add markdown posts to "content/blog" (or the
          directory you specified for the "gatsby-source-filesystem" plugin in
          gatsby-config.js).
        </p>
      </Layout>
    )
  }

  return (
    <Layout location={location} title={siteTitle}>
      <Seo title="All posts" />
      <Bio />
      <div className={homeStyles.row}>
        <section className={homeStyles.section}>
          <h2>‚ú® Javascript</h2>
          <ol style={{ listStyle: `none` }}>
            {jsPosts.map(post => {
              const title = post.frontmatter.title || post.fields.slug
              console.log(post)

              return (
                <li key={post.fields.slug}>
                  <article
                    className="post-list-item"
                    itemScope
                    itemType="http://schema.org/Article"
                  >
                    <header>
                      <p>
                        <Link to={`${post.fields.slug}`} itemProp="url">
                          <span itemProp="headline">{title}</span>
                        </Link>
                      </p>
                      <small>{post.frontmatter.date}</small>
                    </header>
                  </article>
                </li>
              )
            })}
          </ol>
        </section>
        <section className={homeStyles.section}>
          <h2>üßæ Bug Report</h2>
          <ol style={{ listStyle: `none` }}>
            {bugListPosts.map(post => {
              const title = post.frontmatter.title || post.fields.slug
              console.log(post)

              return (
                <li key={post.fields.slug}>
                  <article
                    className="post-list-item"
                    itemScope
                    itemType="http://schema.org/Article"
                  >
                    <header>
                      <p>
                        <Link to={`${post.fields.slug}`} itemProp="url">
                          <span itemProp="headline">{title}</span>
                        </Link>
                      </p>
                      <small>{post.frontmatter.date}</small>
                    </header>
                  </article>
                </li>
              )
            })}
          </ol>
        </section>
      </div>
      <h2>üôÑ Review</h2>
      <ol style={{ listStyle: `none` }}>
        {reviewPosts.map(post => {
          const title = post.frontmatter.title || post.fields.slug
          console.log(post)

          return (
            <li key={post.fields.slug}>
              <article
                className="post-list-item"
                itemScope
                itemType="http://schema.org/Article"
              >
                <header>
                  <p>
                    <Link to={`${post.fields.slug}`} itemProp="url">
                      <span itemProp="headline">{title}</span>
                    </Link>
                  </p>
                  <small>{post.frontmatter.date}</small>
                </header>
                {/* <section>
                  <p
                    dangerouslySetInnerHTML={{
                      __html: post.frontmatter.description || post.excerpt,
                    }}
                    itemProp="description"
                  />
                </section> */}
              </article>
            </li>
          )
        })}
      </ol>

      {/* <h2>Docker</h2>
      <ol style={{ listStyle: `none` }}>
        {dockerPosts.map(post => {
          const title = post.frontmatter.title || post.fields.slug
          console.log(post)

          return (
            <li key={post.fields.slug}>
              <article
                className="post-list-item"
                itemScope
                itemType="http://schema.org/Article"
              >
                <header>
                  <p>
                    <Link to={`${post.fields.slug}`} itemProp="url">
                      <span itemProp="headline">{title}</span>
                    </Link>
                  </p>
                  <small>{post.frontmatter.date}</small>
                </header>
              </article>
            </li>
          )
        })}
      </ol> */}
    </Layout>
  )
}

export default BlogIndex

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }

    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      nodes {
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
        }
      }
    }
  }
`
